@import url(https://fonts.googleapis.com/css2?family=Balsamiq+Sans&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Sniglet&display=swap);
:root {
  --orange-color: #ff9424;
  --orange-color-lite: #ffa230;
  --orange-bg: rgb(255, 170, 64);
  --long: 100px;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu",
    "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: rgb(255, 170, 64);
  background-color: var(--orange-bg);
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace;
}
* {
  box-sizing: border-box;
  outline: none !important;
  /* font-family: "Balsamiq Sans", cursive !important; */
  font-family: "Sniglet", cursive !important;
}
input:-webkit-autofill,
input:-webkit-autofill:hover,
input:-webkit-autofill:focus,
input:-webkit-autofill:active {
  -webkit-transition: background-color 5000s;
  transition: background-color 5000s;
}

::-webkit-scrollbar {
  width: 12px; /* width of the entire scrollbar */
}
::-webkit-scrollbar-track {
  background: transparent; /* color of the tracking area */
}
::-webkit-scrollbar-thumb {
  background-color: white; /* color of the scroll thumb */
  border-radius: 20px; /* roundness of the scroll thumb */
}

.server-text {
  white-space: pre-line;
}
.play-again {
  height: var(--long);
  width: var(--long);
  border-radius: 50%;
  background-color: rgb(255, 170, 64);
  background: linear-gradient(-45deg, rgba(0, 0, 0, 0.12), rgba(255, 255, 255, 0.18));
  box-shadow: 12px 12px 16px 0 rgba(0, 0, 0, 0.25), -8px -8px 12px 0 rgba(255, 255, 255, 0.3);
  border: none;
  margin: 20px;
}

.down {
  transform: scale(1.001);
  box-shadow: 5px 5px 15px rgba(0, 0, 0, 0.2), -5px -5px 15px rgba(255, 255, 255, 0.05),
    inset -5px -5px 10px rgba(255, 255, 255, 0.05), inset 5px 5px 10px rgba(0, 0, 0, 0.2);
}

.regular {
  font-size: 20px;
  transition: all 0.5s;
  white-space: pre-line;
}

.word-bold {
  font-size: 30px;
  font-weight: bold;
  transition: all 0.5s;
}

.hidden {
  opacity: 0;
}

.chat {
  width: 40%;
  min-width: 400px;
  display: flex;
  flex-direction: column;
  padding: 10px;
  overflow-y: auto;
  height: 100%;
}

.message {
  border-radius: 20px;
  padding: 8px 15px;
  margin-top: 5px;
  margin-bottom: 5px;
  display: inline-block;
  white-space: pre-line;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
}

.exercise {
  align-items: flex-start;
}

.exercise {
  margin-right: 25%;
  background-color: #eee;
  position: relative;
}

.exercise:before {
  content: "";
  position: absolute;
  z-index: 0;
  bottom: 0;
  left: -7px;
  height: 20px;
  width: 20px;
  background: #eee;
  border-bottom-right-radius: 15px;
}
.exercise:after {
  content: "";
  position: absolute;
  z-index: 1;
  bottom: 0;
  left: -10px;
  width: 10px;
  height: 20px;
  background: var(--orange-bg);
  border-bottom-right-radius: 10px;
}

.answer {
  align-items: flex-end;
}

.answer {
  color: white;
  margin-left: auto;
  /* background: linear-gradient(to bottom, #00d0ea 0%, #0085d1 100%); */
  background: #0085d1;

  /* background-attachment: fixed; */
  position: relative;
}

.answer:before {
  content: "";
  position: absolute;
  z-index: 0;
  bottom: 0;
  right: -8px;
  height: 20px;
  width: 20px;
  /* background: linear-gradient(to bottom, #00d0ea 0%, #0085d1 100%); */
  background: #0085d1;

  /* background-attachment: fixed; */
  border-bottom-left-radius: 15px;
}

.answer:after {
  content: "";
  position: absolute;
  z-index: 1;
  bottom: 0;
  right: -10px;
  width: 10px;
  height: 20px;
  background: var(--orange-bg);
  border-bottom-left-radius: 10px;
}
.bold {
  font-weight: bold;
}

