.signup-page {
  height: 100vh;
  width: 100vw;
  background-color: white;
  display: flex;
}

.signup-form {
  width: 70%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-around;
  height: 400px;
}
.signup-background {
  background-image: url(background.svg);
  background-repeat: no-repeat;
  background-size: cover;
  width: 40vw;
  min-width: 500px;
  height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  animation: transX 0.5s 1 0s ease-in-out;
}
.fname-lname {
  display: flex;
  justify-content: space-between;
}
.fname-lname :nth-child(1) {
  margin-right: 5px;
}
.signup-title {
  text-transform: uppercase;
  font-weight: 1000;
  font-size: 130%;
  padding-bottom: 3%;
}
.signup-error {
  background-color: rgb(244, 67, 54);
  width: 70%;
  color: #fff;
  font-weight: 500;
  line-height: 1.43;
  border-radius: 4px;
  letter-spacing: 0.01071em;
  display: flex;
  align-items: center;
  padding: 6px 16px;
  box-shadow: 0px 3px 1px -2px rgba(0, 0, 0, 0.2), 0px 2px 2px 0px rgba(0, 0, 0, 0.14),
    0px 1px 5px 0px rgba(0, 0, 0, 0.12);
}
.signup-error > span {
  padding-left: 10px;
}
@keyframes transX {
  0% {
    transform: translateX(-40vw);
  }
  100% {
    transform: rotate(xx) translateX(0);
  }
}
@keyframes opacityAnimate {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
